<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
                placeholder="监测对象、设备名称、设备编号过滤"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="builds"
                :row-class-name="$tableRowClassName"
                v-loading="listLoading"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="siteshortname"
                    label="监测对象"
                    min-width="2"
                >
                </el-table-column>
                <el-table-column prop="sename" label="设备名称" min-width="2">
                </el-table-column>
                <el-table-column prop="setype" label="设备类型" min-width="1">
                    <nobr slot-scope="scope">
                        {{ scope.row.setype ? msetype[scope.row.setype] : "" }}
                    </nobr>
                </el-table-column>
                <el-table-column
                    prop="secode"
                    label="设备编号"
                    show-overflow-tooltip
                    min-width="2"
                ></el-table-column>
                <el-table-column
                    prop="liftovercode"
                    label="AI绑定"
                    min-width="2"
                ></el-table-column>
                <el-table-column
                    prop="checkintime"
                    label="最后上线时间"
                    min-width="2"
                ></el-table-column>
                <el-table-column prop="" label="信息图片" width="90">
                    <template slot-scope="scope">
                        <el-link
                            type="primary"
                            @click="showPic(scope.$index, scope.row)"
                            >查看</el-link
                        >
                    </template>
                </el-table-column>
                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>
            <!-- 图片界面 -->
            <el-dialog
                title="图片"
                :visible.sync="imgModalVisible"
                :close-on-click-modal="false"
                @close="dialogClose"
                class="file-dialog"
                top="5vh"
            >
                <el-form
                    label-position="top"
                    label-width="150px"
                    ref="eqpImgForm"
                    :model="eqpImgForm"
                >
                    <el-form-item label="安装-告知书" prop="notify">
                        <el-upload
                            ref="uploadNotify"
                            action="notify"
                            :limit="10"
                            :on-remove="
                                (file, fileList) => {
                                    handleRemove(file, fileList, 'notify');
                                }
                            "
                            accept=".jpg"
                            :auto-upload="true"
                            :http-request="fileUpload"
                            :on-change="
                                (file, fileList) => {
                                    imgFileChanged(file, fileList, 'notify');
                                }
                            "
                            list-type="picture-card"
                            :file-list="eqpImgForm.notify"
                        >
                            <i slot="default" class="el-icon-plus"></i>
                            <div slot="file" slot-scope="{ file }">
                                <el-image
                                    class="el-upload-list__item-thumbnail"
                                    :src="file.url"
                                    fit="cover"
                                ></el-image>
                                <span class="el-upload-list__item-actions">
                                    <span
                                        class="el-upload-list__item-preview"
                                        @click="handleOpenFile(file)"
                                    >
                                        <i class="el-icon-zoom-in"></i>
                                    </span>
                                    <span
                                        class="el-upload-list__item-delete"
                                        @click="
                                            handleRemoveImgFile(file, 'notify')
                                        "
                                    >
                                        <i class="el-icon-delete"></i>
                                    </span>
                                </span>
                            </div>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="安装-监测报告" prop="monitor">
                        <el-upload
                            ref="uploadMonitor"
                            action="monitor"
                            :limit="10"
                            :on-remove="
                                (file, fileList) => {
                                    handleRemove(file, fileList, 'monitor');
                                }
                            "
                            accept=".jpg"
                            :auto-upload="true"
                            :http-request="fileUpload"
                            :on-change="
                                (file, fileList) => {
                                    imgFileChanged(file, fileList, 'monitor');
                                }
                            "
                            list-type="picture-card"
                            :file-list="eqpImgForm.monitor"
                        >
                            <i slot="default" class="el-icon-plus"></i>
                            <div slot="file" slot-scope="{ file }">
                                <el-image
                                    class="el-upload-list__item-thumbnail"
                                    :src="file.url"
                                    fit="cover"
                                ></el-image>
                                <span class="el-upload-list__item-actions">
                                    <span
                                        class="el-upload-list__item-preview"
                                        @click="handleOpenFile(file)"
                                    >
                                        <i class="el-icon-zoom-in"></i>
                                    </span>
                                    <span
                                        class="el-upload-list__item-delete"
                                        @click="
                                            handleRemoveImgFile(file, 'monitor')
                                        "
                                    >
                                        <i class="el-icon-delete"></i>
                                    </span>
                                </span>
                            </div>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="安装-使用许可证" prop="licenses">
                        <el-upload
                            ref="uploadLicenses"
                            action="licenses"
                            :limit="10"
                            :on-remove="
                                (file, fileList) => {
                                    handleRemove(file, fileList, 'licenses');
                                }
                            "
                            accept=".jpg"
                            :auto-upload="true"
                            :http-request="fileUpload"
                            :on-change="
                                (file, fileList) => {
                                    imgFileChanged(file, fileList, 'licenses');
                                }
                            "
                            list-type="picture-card"
                            :file-list="eqpImgForm.licenses"
                        >
                            <i slot="default" class="el-icon-plus"></i>
                            <div slot="file" slot-scope="{ file }">
                                <el-image
                                    class="el-upload-list__item-thumbnail"
                                    :src="file.url"
                                    fit="cover"
                                ></el-image>
                                <span class="el-upload-list__item-actions">
                                    <span
                                        class="el-upload-list__item-preview"
                                        @click="handleOpenFile(file)"
                                    >
                                        <i class="el-icon-zoom-in"></i>
                                    </span>
                                    <span
                                        class="el-upload-list__item-delete"
                                        @click="
                                            handleRemoveImgFile(
                                                file,
                                                'licenses'
                                            )
                                        "
                                    >
                                        <i class="el-icon-delete"></i>
                                    </span>
                                </span>
                            </div>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="安装-维修保养记录" prop="repair">
                        <el-upload
                            ref="uploadRepair"
                            action="repair"
                            :limit="10"
                            :on-remove="
                                (file, fileList) => {
                                    handleRemove(file, fileList, 'repair');
                                }
                            "
                            accept=".jpg"
                            :auto-upload="true"
                            :http-request="fileUpload"
                            :on-change="
                                (file, fileList) => {
                                    imgFileChanged(file, fileList, 'repair');
                                }
                            "
                            list-type="picture-card"
                            :file-list="eqpImgForm.repair"
                        >
                            <i slot="default" class="el-icon-plus"></i>
                            <div slot="file" slot-scope="{ file }">
                                <el-image
                                    class="el-upload-list__item-thumbnail"
                                    :src="file.url"
                                    fit="cover"
                                ></el-image>
                                <span class="el-upload-list__item-actions">
                                    <span
                                        class="el-upload-list__item-preview"
                                        @click="handleOpenFile(file)"
                                    >
                                        <i class="el-icon-zoom-in"></i>
                                    </span>
                                    <span
                                        class="el-upload-list__item-delete"
                                        @click="
                                            handleRemoveImgFile(file, 'repair')
                                        "
                                    >
                                        <i class="el-icon-delete"></i>
                                    </span>
                                </span>
                            </div>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="拆除-拆除告知" prop="dismantle">
                        <el-upload
                            ref="uploadDismantle"
                            action="dismantle"
                            :limit="10"
                            :on-remove="
                                (file, fileList) => {
                                    handleRemove(file, fileList, 'dismantle');
                                }
                            "
                            accept=".jpg"
                            :auto-upload="true"
                            :http-request="fileUpload"
                            :on-change="
                                (file, fileList) => {
                                    imgFileChanged(file, fileList, 'dismantle');
                                }
                            "
                            list-type="picture-card"
                            :file-list="eqpImgForm.dismantle"
                        >
                            <i slot="default" class="el-icon-plus"></i>
                            <div slot="file" slot-scope="{ file }">
                                <el-image
                                    class="el-upload-list__item-thumbnail"
                                    :src="file.url"
                                    fit="cover"
                                ></el-image>
                                <span class="el-upload-list__item-actions">
                                    <span
                                        class="el-upload-list__item-preview"
                                        @click="handleOpenFile(file)"
                                    >
                                        <i class="el-icon-zoom-in"></i>
                                    </span>
                                    <span
                                        class="el-upload-list__item-delete"
                                        @click="
                                            handleRemoveImgFile(
                                                file,
                                                'dismantle'
                                            )
                                        "
                                    >
                                        <i class="el-icon-delete"></i>
                                    </span>
                                </span>
                            </div>
                        </el-upload>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="imgModalVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="addPicSubmit"
                        :loading="eqpImgFormLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="editForm"
                    label-width="120px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-col :span="12">
                        <el-form-item label="监测对象分组" prop="GROUPID">
                            <el-select
                                v-model="editForm.GROUPID"
                                filterable
                                placeholder="请选择"
                                @change="groupChange"
                            >
                                <el-option
                                    v-for="item in groups"
                                    :key="item.GROUPID"
                                    :label="item.groupshortname"
                                    :value="item.GROUPID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="监测对象" prop="SITEID">
                            <el-select
                                v-model="editForm.SITEID"
                                filterable
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in selectSites"
                                    :key="item.SITEID"
                                    :label="item.siteshortname"
                                    :value="item.SITEID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="特种设备名称" prop="sename">
                            <el-input
                                v-model="editForm.sename"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="特种设备类型" prop="setype">
                            <el-select
                                v-model="editForm.setype"
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="(value, key) in msetype"
                                    :key="key"
                                    :label="value"
                                    :value="Number(key)"
                                ></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设备编号" prop="secode">
                            <el-input
                                v-model="editForm.secode"
                                auto-complete="off"
                                maxlength="100"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="AI绑定编号" prop="liftovercode">
                            <el-input
                                v-model="editForm.liftovercode"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <!-- <el-col :span="12">
                        <el-form-item label="上次检修日期" prop="lastcheckday">
                            <el-date-picker
                                v-model="editForm.lastcheckday"
                                type="date"
                                placeholder="选择日期"
                            >
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="下次检修日期" prop="nextcheckday">
                            <el-date-picker
                                disabled
                                v-model="editForm.nextcheckday"
                                type="date"
                                placeholder="选择日期"
                            >
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="生产商" prop="maker">
                            <el-input
                                v-model="editForm.maker"
                                auto-complete="off"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="生产日期" prop="productdate">
                            <el-date-picker
                                v-model="editForm.productdate"
                                type="date"
                                placeholder="选择日期"
                            >
                            </el-date-picker>
                        </el-form-item>
                    </el-col> -->
                    <el-col :span="24">
                        <el-form-item label="备注" prop="remark">
                            <el-input
                                type="textarea"
                                placeholder="请输入内容"
                                v-model="editForm.remark"
                                maxlength="100"
                                show-word-limit
                            >
                            </el-input>
                        </el-form-item>
                    </el-col>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="editFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--新增界面-->
            <el-dialog
                title="新增"
                :visible.sync="addFormVisible"
                v-model="addFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="addForm"
                    label-width="120px"
                    :rules="addFormRules"
                    ref="addForm"
                >
                    <el-col :span="12">
                        <el-form-item label="监测对象分组" prop="GROUPID">
                            <el-select
                                v-model="addForm.GROUPID"
                                filterable
                                placeholder="请选择"
                                @change="groupChange"
                            >
                                <el-option
                                    v-for="item in groups"
                                    :key="item.GROUPID"
                                    :label="item.groupshortname"
                                    :value="item.GROUPID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="监测对象" prop="SITEID">
                            <el-select
                                v-model="addForm.SITEID"
                                filterable
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in selectSites"
                                    :key="item.SITEID"
                                    :label="item.siteshortname"
                                    :value="item.SITEID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="特种设备名称" prop="sename">
                            <el-input
                                v-model="addForm.sename"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="特种设备类型" prop="setype">
                            <el-select
                                v-model="addForm.setype"
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="(value, key) in msetype"
                                    :key="key"
                                    :label="value"
                                    :value="Number(key)"
                                ></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设备编号" prop="secode">
                            <el-input
                                v-model="addForm.secode"
                                auto-complete="off"
                                maxlength="100"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="AI绑定编号" prop="liftovercode">
                            <el-input
                                v-model="addForm.liftovercode"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <!-- <el-col :span="12">
                        <el-form-item label="上次检修日期" prop="lastcheckday">
                            <el-date-picker
                                v-model="addForm.lastcheckday"
                                type="date"
                                placeholder="选择日期"
                            >
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="下次检修日期" prop="nextcheckday">
                            <el-date-picker
                                disabled
                                v-model="addForm.nextcheckday"
                                type="date"
                                placeholder="选择日期"
                            >
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="生产商" prop="maker">
                            <el-input
                                v-model="addForm.maker"
                                auto-complete="off"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="生产日期" prop="productdate">
                            <el-date-picker
                                v-model="addForm.productdate"
                                type="date"
                                placeholder="选择日期"
                            >
                            </el-date-picker>
                        </el-form-item>
                    </el-col> -->
                    <el-col :span="24">
                        <el-form-item label="备注" prop="remark">
                            <el-input
                                type="textarea"
                                placeholder="请输入内容"
                                v-model="addForm.remark"
                                maxlength="100"
                                show-word-limit
                            >
                            </el-input>
                        </el-form-item>
                    </el-col>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="addFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="addSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    getspecialeqppagelist,
    getspecialeqpGroups,
    removespecialeqp,
    editspecialeqp,
    addspecialeqp,
    postfile,
    setsepic,
    getsepics,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
import { msetype } from "../../assets/dic/dic";
export default {
    components: { leftgroup, Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            groupCounts: [],
            buttonList: [],
            filters: {
                name: "",
            },
            builds: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                sename: [
                    {
                        required: true,
                        message: "请填写设备名称",
                        trigger: "blur",
                    },
                ],
                setype: [
                    {
                        required: true,
                        message: "请选择设备类型",
                        trigger: "blur",
                    },
                ],
                secode: [
                    {
                        required: true,
                        message: "请填写设备编号",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {},
            addFormVisible: false,
            addLoading: false,
            addFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                sename: [
                    {
                        required: true,
                        message: "请填写设备名称",
                        trigger: "blur",
                    },
                ],
                setype: [
                    {
                        required: true,
                        message: "请选择设备类型",
                        trigger: "blur",
                    },
                ],
                secode: [
                    {
                        required: true,
                        message: "请填写设备编号",
                        trigger: "blur",
                    },
                ],
            },
            addForm: {},
            canEdit: false,
            canDelete: false,
            groups: [],
            selectSites: [],
            selectGroupId: 0,
            msetype: msetype,
            imgModalVisible: false,
            eqpImgFormLoading: false,
            eqpImgForm: {
                notify: [],
                monitor: [],
                licenses: [],
                repair: [],
                dismantle: [],
                notifyDel: [],
                monitorDel: [],
                licensesDel: [],
                repairDel: [],
                dismantleDel: [],
            },
            showpicrow: {},
        };
    },
    methods: {
        addPicSubmit: function () {
            let _this = this;

            //this.$confirm("确认提交吗？", "提示", {}).then(() => {
            this.eqpImgFormLoading = true;

            let { imgurl, ...para } = Object.assign(
                { SEID: this.showpicrow.SEID },
                this.eqpImgForm
            );

            setsepic(para).then((res) => {
                if (res.success) {
                    this.$message({
                        message: "设置成功",
                        type: "success",
                    });
                    this.$refs["eqpImgForm"].resetFields();
                    this.imgModalVisible = false;
                    //this.getPageList();
                    //this.getGroupCounts();
                } else {
                    this.$message({
                        message: res.msg,
                        type: "error",
                    });
                }
                this.eqpImgFormLoading = false;
            });
            //});
        },
        dialogClose() {
            this.$refs["eqpImgForm"].resetFields();
        },
        showPic(index, row) {
            this.imgModalVisible = true;
            this.showpicrow = row;
            this.eqpImgForm.notify = [];
            this.eqpImgForm.monitor = [];
            this.eqpImgForm.licenses = [];
            this.eqpImgForm.repair = [];
            this.eqpImgForm.dismantle = [];

            this.eqpImgForm.notifyDel = [];
            this.eqpImgForm.monitorDel = [];
            this.eqpImgForm.licensesDel = [];
            this.eqpImgForm.repairDel = [];
            this.eqpImgForm.dismantleDel = [];

            let param = { SEID: row.SEID };

            getsepics(param).then((res) => {
                if (res.success) {
                    if (res.data) {
                        res.data.forEach((e) => {
                            switch (e.filetype) {
                                case 1:
                                    this.eqpImgForm.notify.push(e);
                                    break;
                                case 2:
                                    this.eqpImgForm.monitor.push(e);
                                    break;
                                case 3:
                                    this.eqpImgForm.licenses.push(e);
                                    break;
                                case 4:
                                    this.eqpImgForm.repair.push(e);
                                    break;
                                case 5:
                                    this.eqpImgForm.dismantle.push(e);
                                    break;
                            }
                        });
                    }
                }
            });
        },
        imgFileChanged(file, fileList, type) {
            switch (type) {
                case "notify":
                    this.eqpImgForm.notify = fileList;
                    break;
                case "monitor":
                    this.eqpImgForm.monitor = fileList;
                    break;
                case "licenses":
                    this.eqpImgForm.licenses = fileList;
                    break;
                case "repair":
                    this.eqpImgForm.repair = fileList;
                    break;
                case "dismantle":
                    this.eqpImgForm.dismantle = fileList;
                    break;
            }
        },
        handleRemove(file, fileList, type) {
            switch (type) {
                case "notify":
                    this.eqpImgForm.notify = fileList;
                    this.eqpImgForm.notifyDel.push(file.url);
                    break;
                case "monitor":
                    this.eqpImgForm.monitor = fileList;
                    this.eqpImgForm.monitorDel.push(file.url);
                    break;
                case "licenses":
                    this.eqpImgForm.licenses = fileList;
                    this.eqpImgForm.licensesDel.push(file.url);
                    break;
                case "repair":
                    this.eqpImgForm.repair = fileList;
                    this.eqpImgForm.repairDel.push(file.url);
                    break;
                case "dismantle":
                    this.eqpImgForm.dismantle = fileList;
                    this.eqpImgForm.dismantleDel.push(file.url);
                    break;
            }
        },
        handleRemoveImgFile(file, type) {
            switch (type) {
                case "notify":
                    this.eqpImgForm.notify = this.eqpImgForm.notify.filter(
                        (item) => {
                            return item.uid !== file.uid;
                        }
                    );
                    this.eqpImgForm.notifyDel.push(file.url);
                    break;
                case "monitor":
                    this.eqpImgForm.monitor = this.eqpImgForm.monitor.filter(
                        (item) => {
                            return item.uid !== file.uid;
                        }
                    );
                    this.eqpImgForm.monitorDel.push(file.url);
                    break;
                case "licenses":
                    this.eqpImgForm.licenses = this.eqpImgForm.licenses.filter(
                        (item) => {
                            return item.uid !== file.uid;
                        }
                    );
                    this.eqpImgForm.licensesDel.push(file.url);
                    break;
                case "repair":
                    this.eqpImgForm.repair = this.eqpImgForm.repair.filter(
                        (item) => {
                            return item.uid !== file.uid;
                        }
                    );
                    this.eqpImgForm.repairDel.push(file.url);
                    break;
                case "dismantle":
                    this.eqpImgForm.dismantle =
                        this.eqpImgForm.dismantle.filter((item) => {
                            return item.uid !== file.uid;
                        });
                    this.eqpImgForm.dismantleDel.push(file.url);
                    break;
            }
        },
        fileUpload(param) {
            let fileObj = param.file; // 文件对象
            let form = new FormData(); // FormData 对象
            form.append("formFiles", fileObj);
            postfile(form)
                .then((res) => {
                    switch (param.action) {
                        case "notify":
                            this.eqpImgForm.notify[
                                this.eqpImgForm.notify.length - 1
                            ].filename = res.data;
                            break;
                        case "monitor":
                            this.eqpImgForm.monitor[
                                this.eqpImgForm.monitor.length - 1
                            ].filename = res.data;
                            break;
                        case "licenses":
                            this.eqpImgForm.licenses[
                                this.eqpImgForm.licenses.length - 1
                            ].filename = res.data;
                            break;
                        case "repair":
                            this.eqpImgForm.repair[
                                this.eqpImgForm.repair.length - 1
                            ].filename = res.data;
                            break;
                        case "dismantle":
                            this.eqpImgForm.dismantle[
                                this.eqpImgForm.dismantle.length - 1
                            ].filename = res.data;
                            break;
                    }
                })
                .catch((err) => {});
        },
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getPageList();
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getPageList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                groupid: this.selectGroupId,
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getspecialeqppagelist(paras).then((res) => {
                if (res.success) {
                    this.builds = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getPageList(val);
        },
        getGroupCounts() {
            getspecialeqpGroups().then((res) => {
                if (res.success) {
                    this.groupCounts = res.data;
                }
            });
        },
        groupChange(val) {
            this.getGroupSites(val);
            this.$set(this.addForm, "SITEID", "");
            this.$set(this.editForm, "SITEID", "");
            this.selectSites = this.groupSites;
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.SEID };
                    removespecialeqp(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getPageList(this.page);
                        this.getGroupCounts();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            this.editFormVisible = true;
            this.groupChange(row.GROUPID);
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = {};
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    editspecialeqp(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["editForm"].resetFields();
                            this.editFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.editLoading = false;
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let para = Object.assign({}, this.addForm);
                    addspecialeqp(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.$refs["addForm"].resetFields();
                            this.addFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.addLoading = false;
                    });
                    //});
                }
            });
        },
        handleOpenFile(file) {
            window.open(file.url);
        },
    },
    mounted() {
        this.getPageList();
        this.getGroupCounts();
        this.getGroups();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
    width: 100%;
}
.file-dialog /deep/ .el-dialog__body {
    padding-top: 0px;
    padding-bottom: 0px;
}
.file-dialog /deep/ .el-upload-list--picture-card .el-upload-list__item,
.file-dialog /deep/ .el-upload--picture-card {
    height: 70px;
    line-height: 74px;
    width: 70px;
}
.file-dialog /deep/ .el-form-item {
    margin-bottom: 0px;
}
.file-dialog /deep/ .el-upload-list__item {
    transition: none !important;
}
.file-dialog /deep/ .el-upload-list__item-status-label {
    display: none;
}
.file-dialog /deep/ .el-upload-list__item > div {
    height: 100%;
}
</style>


